export default {
  props: {
    size: {
      type: String,
      default: 'normal',
      validator(value) {
        const sizeArray = ['mini', 'small', 'normal', 'large'];
        if (!sizeArray.includes(value)) {
          // eslint-disable-next-line no-console
          console.warn('size 属性只能为mini|small|normal|large');
          return 'normal';
        }
        return value;
      }
    }
  }
};
